<script>
    import QRCodeStyling from 'src/application/services/qr-code-styling.js';
    import {onMount} from "svelte";

    export let text;

    export let qrCode = new QRCodeStyling({
        width: 250,
        height: 250,
        type: "svg",
        data: $text,
        // image: "frontend/web/src/elements/assets/mik_favico.svg",
        image: "data: image/png;base64,",
        dotsOptions: {
            color: "#6a1a4c",
            type: "dots",
        },
        backgroundOptions: {
            color: "#ffffff",
            gradient: null
        },
        imageOptions: {
            crossOrigin: "anonymous",
            margin: 2,
            hideBackgroundDots: true,
            imageSize: 0.5
        },
        dotsOptionsHelper: {
            colorType: {
                single: true,
                gradient: false
            }
        },
        cornersSquareOptions: {
            type: "extra-rounded",
            color: "#000000",
        },
        cornersDotOptions: {
            "type": "square",
            "color": "#000000",
        }
    });


    onMount(async () => {
        qrCode.append(document.getElementById("canvas"));
    });
</script>


<div class="content">
    <div class="canvasHolder" id="canvas"></div>
</div>


<style>
    .canvasHolder {
        width: 250px;
        margin: 0 auto 0 auto;
    }

    body {
        background-size: cover;
    }
</style>